
import {mapGetters} from 'vuex'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'


export default {
computed: mapGetters([
'selectBasicInformationData',
]),
components: {
Cell,
Group,
Toast,
XHeader,
InlineLoading,
Flexbox,
FlexboxItem,
Actionsheet
},
data() {
return {
UserData: '',
showError: false,
showErrorContent: '',
showLoading: false,
menus: {
menu1: '',
menu2: ''
},
showMenus: false,
}
},
methods: {
clickMenu(m, n) {
if (n == '') {
this.$router.push({name: 'Login'});
}
if (n == '') {
this.$router.push({name: 'UpdateInformation'});
}
},

initData() {
var selectBasicInformation = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"ts_ai_ConstructionTeam": ''
}
this.$store.dispatch('initSelectBasicInformation', selectBasicInformation)
.then(() => {
this.showLoading = false;
}, err => {
this.showError = true;
this.showErrorContent = err;
})
},
onClick(item) {
this.$router.push({name: 'ProjectSituationDetails'})
sessionStorage.setItem('TeamName', JSON.stringify(item.ts_ai_ConstructionTeam))
sessionStorage.setItem('ItemId', JSON.stringify(item.ts_ai_Id))
}
},
created() {
if (!sessionStorage.getItem('UserInfo')) {
this.$router.push({name: 'Login'})
}
this.showLoading = true;
this.UserData = JSON.parse(sessionStorage.getItem('UserInfo'));
this.initData();
this.$parent.showDetils = false;
}
}



