
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
computed: mapGetters([
'checkRecordDetailsObj'
]),
components: {
XHeader,
Group,
Cell,
XInput,
Calendar,
Box,
XButton,
Toast,
InlineLoading,
XTextarea
},
data() {
return {
readonly: false,
isShowLoading: false,
showError: false,
showErrorContent: '',
showLoading: false,
}
},
methods: {
updateCheckRecordSubmit() {
var updateRecords = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"data": this.checkRecordDetailsObj
}
this.$store.dispatch('updateCheckRecordSubmit', updateRecords)
.then(() => {
this.$router.push({name: 'CheckRecordDetils'});
this.isShowLoading = true;
}, err => {
this.showError = true;
this.showErrorContent = err;
})
}
},
mounted() {
if (this.checkRecordDetailsObj) {
this.showLoading = false;
}
},
updated() {
if (this.checkRecordDetailsObj) {
this.showLoading = false;
}
},
created() {
this.showLoading = true;
}
}



