
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'


export default {
computed: mapGetters([
'checkRecordDetailsObj'
]),
components: {
XHeader,
Group,
Cell,
Toast,
Confirm,
InlineLoading
},
data() {
return {
showError: false,
showErrorContent: '',
showDelete: false,
showLoading: false,
functionShow: true,
}
},
methods: {

updateCheck() {
this.$router.push({name: 'UpdateCheckRecord'})
},

deleteCheck() {
this.showDelete = true;
},
onCancel() {
this.showDelete = false;
},
onConfirm() {
let deleteRecords = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"ts_ai_Id": this.checkRecordDetailsObj.ts_ai_Id
}
this.$store.dispatch('deleteCheck', deleteRecords)
.then(() => {
this.$router.push({name: 'ProjectSituationDetails'})
window.location.reload()
}, err => {
this.showError = true;
this.showErrorContent = err;
})

},

goTop(){
this.$router.push({name:'InspectProject'})
}
},
updated() {
if (this.checkRecordDetailsObj) {
this.showLoading = false;
}
},
created() {
this.showLoading = true;
if (JSON.parse(sessionStorage.getItem('UserInfo')).ts_ai_Ownership != JSON.parse(sessionStorage.getItem('TeamName'))) {
this.functionShow = false;
}
}
}



