
import {mapGetters} from 'vuex'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
Toast
},
computed: mapGetters([]),
data() {
return {
accountValue: '',
passworldValue: '',
showError: false,
showErrorContent: ''
}
},
methods: {
loginSubmit() {
let loginAccountNumbe = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"ts_ai_Login": this.accountValue,
"ts_ai_Password": this.passworldValue
}
this.$store.dispatch('loginSubmit', loginAccountNumbe)
.then((data) => {
this.$router.push({name: 'ProjectSituation'})
sessionStorage.setItem('UserInfo', JSON.stringify(data));
}, err => {
this.showError = true;
this.showErrorContent = err;
})
},
signIn() {
this.$router.push({name: 'SignIn'})
}
},
mounted() {
}
}



