
import {mapGetters} from 'vuex'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
computed: mapGetters([
'updateProjectObj'
]),
components: {
XInput,
Group,
XHeader,
Calendar,
XButton,
Box,
Toast,
InlineLoading,
XTextarea
},
data() {
return {
readonly: false,
isUpdateLoading: false,
showError: false,
showErrorContent: '',
showLoading: false
}
},
methods: {

updateSubmit() {
let updateBasicInformation = {
"loginUserID": "huileyou",
"loginUserPass": "123",
"data": this.updateProjectObj
}
this.$store.dispatch('updateSubmit', updateBasicInformation)
.then(() => {
this.$router.push({name: 'ProjectSituationDetails'});
this.isUpdateLoading = true;
}, err => {
this.showErrorContent = err;
this.showError = true;
})
},

goTop(){
this.$router.push({name:'ProjectSituationDetails'})
}
},
mounted(){
if( this.updateProjectObj ){
this.showLoading = false;
}
},
created() {
this.showLoading = true;
}
}



