
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'


export default {
computed: mapGetters([]),
components: {
XHeader,
XInput,
Cell,
Group,
Calendar,
Box,
XButton,
XTextarea
},
data() {
return {
insertRecords: {
"loginUserID": "huileyou",
"loginUserPass": "123",
"data": {
"ts_ai_InspectionTime": "",
"ts_ai_Item": "",
"ts_ai_ProjectPro": "",
"ts_ai_Status": "",
"ts_ai_ProcessingStatus": "",
"ts_ai_Inspector": "",
"ts_ai_Construction": "",
"ts_ai_Supervision": "",
"ts_ai_Engineering": "",
"ts_ai_ConstructionTeam": "",
}
},
readonly:false,
isShowLoading: false,
}
},
methods: {
initData() {
},
search() {
this.initData()
},
addSubmit(){
this.$store.dispatch('addCheckRecordSubmit',this.insertRecords)
.then(()=>{
this.$router.push({name:'ProjectSituationDetails'})
})
},

goTop(){
this.$router.push({name:'ProjectSituationDetails'})
}
},
created(){
this.insertRecords.data.ts_ai_Item = JSON.parse(sessionStorage.getItem('ItemId'));
this.insertRecords.data.ts_ai_ConstructionTeam = JSON.parse(sessionStorage.getItem('TeamName'));
}
}



